import ListMixin from '@/mixin/List.js'
export default {
	name: 'question',
	mixins: [ListMixin],
	methods: {
		getData () {
			this.$http(this.$urlPath.myAskUrl, {
				page: this.page.num
			}, null, (data) => {
				this.loadSuccess(data.data)
			}, (errorCode, error) => {
				this.loadFail()
			}, () => {
				uni.stopPullDownRefresh()
			})
		},
		startMessageInfo (item) {
			this.$router.push({name: 'leaveMessageInfo', query: {s_id: item.sid, aid: item.aid}})
		},
		onRefresh () {
			this.getData()
		}
	}
}
